<div class="portfolio p-mb-6"><h3 class="p-text-center">Portfolio</h3><hr><teleport to="body"><Dialog :header="dialog.title" v-model:visible="dialogIsOpened" :modal="true"><img v-if="dialog.src" :alt="dialog.title" :src="dialog.src"><p class="p-m-0">{{dialog.text}}</p><hr><div class="p-tag p-tag-rounded p-m-1" v-for="tag in dialog.tags" :key="tag">{{tag}}</div></Dialog></teleport><div class="p-grid"><div class="p-col-12 p-md-6" v-for="(item, index) in list" :key="index"><Card @click="openDialog(item)" :class="{more: isLotOfDescription(item.text)}"><template #header><div style="height:160px; overflow: hidden;"><img :alt="item.title" :src="item.src"></div></template><template #title><h4>{{item.title}}</h4></template><template #subtitle><p v-html="item.subtitle"></p></template><template #content>{{getText(item.text)}}<a class="more" v-if="item.text.length &gt; length" @click="openDialog(item)">  More ></a></template><template #footer><hr><Tag v-for="tag in item.tags" :key="tag" :value="tag" rounded></Tag></template></Card></div></div></div>